import React, { useContext, useEffect, useState } from 'react'
import { SocketContext } from '../context/SocketContext';

export const BandList = () => {
    
    const{ socket } = useContext(SocketContext)
    
    const [bandas, setBandas] = useState([]);

    useEffect(() => {
        socket.on('current_band_list', (data) => {
            setBandas(data)
        })
        return ()=> socket.off('current_band_list')
    }, [socket])

    
    const onChange = (e, id) => {
        setBandas( bandas.map(b=>{
            if(b.id === id){
                b.name = e.target.value;
            }
            return b;
        }));
    };

    const onBlur = (id, nombre) => {
        socket.emit('nuevo_nombre', {id, nombre} );
    };

    

    return (
        <>
            {
                bandas.map(({ id, name, votes }) => (
                    <div key={id} className="p-2 text-center">


                        <div className="flex justify-around mb-5">
                            <button 
                                className="mx-2 flex-auto bg-blue-500 p-3 rounded hover:bg-blue-600"
                                onClick={ ()=> socket.emit('votar', id ) }

                            ><i className="text-lg far fa-thumbs-up" ></i></button>

                            <input
                                type="text"
                                className="mx-2 flex-auto rounded text-black px-4 font-bold" 
                                value={name} 
                                onChange={(e)=> onChange(e, id)}
                                onBlur={()=> onBlur(id, name)}
                            />

                            <div className="mx-2 flex flex-col flex-auto">
                                <p className="">Votos</p>
                                <p className="font-bold">{votes}</p>
                            </div>

                            <button 
                                className="mx-2 flex-auto bg-red-500 p-3 rounded hover:bg-red-600"
                                onClick={ ()=> socket.emit('borrar', id ) }
                            ><i className="far fa-trash-alt"></i></button>
                        </div>

                    </div>
                ))
            }
        </>
    )
}
