import React, { useContext, useState } from 'react'
import { SocketContext} from '../context/SocketContext';


export const BandAdd = () => {
    
    
    const [value, setValue] = useState('')

    const{ socket }=useContext(SocketContext)

    const agregar = (e) => {
        e.preventDefault();

         if( value.trim().length>0 ){
             
            socket.emit('nueva_band', value );
            setValue("")
         }
    };

    return (
        <div className="text-black text-center">
            <form className=" p-10" onSubmit={agregar}>
                <label htmlFor="" className="text-2xl inline-block my-4 text-white">Agregar una nueva banda</label>
                <input 
                    type="text" 
                    className="w-full rounded p-3 px-4 text-lg"
                    value={value}
                    onChange={(e)=>setValue(e.target.value)}
                />
                <button className="border-none rounded mt-10 py-4 bg-green-500 w-full text-2xl text-white font-bold hover:bg-green-600">Add <i className="mx-5 fas fa-users"></i></button>
            </form>
        </div>
    )
}
