import { Chart, registerables } from 'chart.js';
import React, { useEffect} from 'react'
import { useGrafico } from '../hooks/useGrafico';

Chart.register(...registerables);

export const ChartBands = () => {

    
    const {bandas: data} = useGrafico();

    useEffect(() => {

        const ctx = document.getElementById('myChart');
            
        const myChart = new Chart(ctx, {
                type: 'bar',
                data: {
                    labels: data.map( da => da.name),
                    datasets: [{
                        label: '# Votos',
                        data: data.map( da => da.votes),
                        backgroundColor: [
                            'rgba(255, 99, 132, 0.2)',
                            'rgba(54, 162, 235, 0.2)',
                            'rgba(255, 206, 86, 0.2)',
                            'rgba(75, 192, 192, 0.2)',
                            'rgba(153, 102, 255, 0.2)',
                            'rgba(255, 159, 64, 0.2)'
                        ],
                        borderColor: [
                            'rgba(255, 99, 132, .9)',
                            'rgba(54, 162, 235, 1)',
                            'rgba(255, 206, 86, 1)',
                            'rgba(75, 192, 192, 1)',
                            'rgba(153, 102, 255, 1)',
                            'rgba(255, 159, 64, 1)'
                        ],
                        borderWidth: 1,
                        animation: false
                        
                    }]
                },
                options: {
                    scales: {
                        // y: {
                        //     beginAtZero: true,
                        // },
                        x: {
                            beginAtZero: true,
                            stacked: true
                        },
                        1:{
                            grid:{
                                borderColor: 'rgba(255, 255, 255, 1)',
                                color: 'rgba(255, 255, 255, .0)',
                            },
                        }
                        
                    },
                    indexAxis: 'y',
                }
        });

        return ()=> {
           myChart.destroy();
        }
    }, [data])


   


    return (
        <div className="">
            <canvas id="myChart"></canvas>
        </div>
    )
}
